.classic-calendar {
  text-rendering: auto;
  box-shadow: 1px 1px 0 #ccc;
  background: #ffffff;
  border: 1px solid #9bc0e0;
  width: 215px;
  outline: none;
  position: relative;
  box-sizing: content-box;
}

.classic-calendar * {
  box-sizing: content-box;
}

.classic-calendar a, .classic-calendar a:hover {
  text-decoration: none;
  cursor: pointer;
}

.classic-pop-up-calendar {
  position: absolute;
  left: -9999px;
  top: -9999px;
}

.classic-calendar-hidden {
  display: none;
}

.classic-pop-up-calendar-hidden {
  display: block;
  visibility: hidden;
  left: -9999px;
  top: -9999px;
}

.classic-calendar-prev-month-btn,
.classic-calendar-next-month-btn,
.classic-calendar-prev-year-btn,
.classic-calendar-next-year-btn,
.classic-calendar-header {
  user-select:none;
  -webkit-user-select:none;
  background-image: url('//gtms04.alicdn.com/tps/i4/TB1.L9bFVXXXXbAXpXXDM4qIXXX-19-300.png');
}

.classic-calendar .classic-calendar-header {
  background-repeat: repeat-x;
  background-position: 0 -240px;
  height: 26px;
  line-height: 26px;
  text-align: center;
}

.classic-calendar-prev-month-btn,
.classic-calendar-next-month-btn,
.classic-calendar-prev-year-btn,
.classic-calendar-next-year-btn {
  text-indent: -9999px;
  width: 19px;
  height: 19px;
  position: absolute;
  top: 2px;
}

.classic-calendar-prev-month-btn {
  background-position: 0 -60px;
  left: 24px;
}

.classic-calendar-prev-month-btn:hover {
  background-position: 0 -80px;
}

.classic-calendar-prev-year-btn {
  background-position: 0 -180px;
  left: 0;
}

.classic-calendar-prev-year-btn:hover {
  background-position: 0 -200px;
}

.classic-calendar-next-month-btn {
  background-position: 0 0;
  right: 24px;
}

.classic-calendar-next-month-btn:hover {
  background-position: 0 -20px;
}

.classic-calendar-next-year-btn {
  background-position: 0 -120px;
  right: 0;
}

.classic-calendar-next-year-btn:hover {
  background-position: 0 -140px;
}

.classic-calendar {
  &-year-select, &-month-select {
    color: #ffffff;
    line-height: 26px;
    border: 1px solid transparent;
    text-decoration: none;
    padding: 0 4px;

    &:hover {
      cursor: pointer;
      color: #f0ff00;
      border: 1px solid #8cc6ff;
      background: url('//gtms04.alicdn.com/tps/i4/TB1.L9bFVXXXXbAXpXXDM4qIXXX-19-300.png');
      background-position: 0 -268px;
    }
  }
}

.classic-calendar-week-number-cell {
  text-align: center;
  line-height: 22px;
}

.classic-calendar-week-number-header {
  text-indent: -9999px;
}

.classic-calendar-table {
  table-layout: fixed;
  width: 100%;
  border-collapse: separate;
}

.classic-calendar-column-header {
  width: 25px;
  color: black;
  font-weight: bold;
  text-align: center;
  padding: 4px 0;
}

.classic-calendar-column-header-inner {
  display: block;
  padding-bottom: 4px;
  border-bottom: 1px dashed #c9c9c9
}

.classic-calendar-cell {
  text-align: center;
}

.classic-calendar-date {
  color: #004499;
  line-height: 22px;
  display: block;
  border-radius: 2px;
  border: 1px solid transparent;
  outline: none;
}

.classic-calendar-last-month-cell .classic-calendar-date,
.classic-calendar-disabled-cell .classic-calendar-date,
.classic-calendar-next-month-btn-day .classic-calendar-date {
  color: #bfbfbf;
}

.classic-calendar-date:hover {
  background-color: #f3f9ff;
  border-color: #88b1ea;
}

.classic-calendar-disabled-cell .classic-calendar-date:hover {
  background-color: #ffffff;
  border-color: transparent;
}

.classic-calendar-disabled-cell .classic-calendar-date {
  cursor: default;
}

.classic-calendar-disabled-cell .classic-calendar-date:hover {
  border-color: transparent;
}

.classic-calendar-today .classic-calendar-date {
  color: #357c00;
  border-color: #7dba4e;
}

.classic-calendar-selected-day .classic-calendar-date {
  color: #fff;
  background-color: #4187e7;
  border-color: #4187e7;
}

.classic-calendar-footer {
  padding: 3px 0;
  background-color: #f5f5f5;
  text-align: center;
}

.classic-calendar-today-btn, .classic-calendar-clear-btn, .classic-calendar-ok-btn {
  line-height: 16px;
  height: 16px;
  margin: 0 3px 0 2px;
  border-radius: 3px;
  padding: 3px 3px 3px 3px;
  border-width: 1px;
  border-style: solid;
  background-color: #4187e7;
  color: #ffffff;
  border-color: #3375cf;
  display: inline-block;
  margin-left: 5px;

  &:hover {
    color: #ffffff;
  }
}

.classic-calendar-footer-btn {
  margin-top: 2px;
  display: block;
}







